import request from '../../utils/request.js'
export default {
	props: ['navId'],
	data() {
		return {
			cateList: []
		};
	},
	async mounted() {
		let cateListData = await request('/getIndexCateListData')
		this.cateList = cateListData.data
	},
	computed: {
		cateObj(){
			return this.cateList.find(item => item.category.parentId === this.navId)
		}
	},
	methods: {
		// 跳转至详情页
		toDetail(cateItem){
			wx.navigateTo({
				url: '/pages/detail/detail?cateItem=' + JSON.stringify(cateItem)
			})
		}
	}
}
